import { MSG_OAP_NOTIFICATION } from "@/app/scripts/app-consts";
import { sendBackgroundMessage } from "@/utils/messaging/messaging";
import { uiLanguage } from '@/utils/locales';

// TODO: can make this a dynamic script using chrome.scripting.executeScript API
// hosts to show the OAP notification on
const notificationHosts = [
    "myaccount.google.com",
];

// check if the current host is in the notificationHosts array
const isNotificationHost = notificationHosts.some(host => window.location.host.includes(host));
// get the locale of the current page


if (isNotificationHost && uiLanguage === "en") {
    console.log("OAP: Notification host detected", window.location.host);
    // TODO: send message to background to show the OAP notification
    sendBackgroundMessage({ type: MSG_OAP_NOTIFICATION }).then(() => {
        console.log("OAP: Notification message sent to background");
    });
}